<template>
    <section>
        <tg-list v-show="IsTicketTypeView" :IsShowAction="false" ModuleName="FSM" SubModuleCode="TicketMapping"
            :LegendArray="[]" :widgets="[]" IdentityColumn="TYPE_ID" :listType="['List']" :ListData="ViewHistoryList"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :RenderRowActionMethod="() => { }" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>

          <searchFilter v-if="showFilter" submodulecode="Ticketing" modulename="Ticketing" v-on:close="showFilter = false"
            v-bind:callbackfunction="fetchRecords" v-on:FilterData="FilterData">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </searchFilter>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import Schema from "@/views/PendingAcceptance/PendingSchema";
export default {
    data() {
        return {
            noRecords: false,
            TicketId: 0,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            ViewHistoryList: [],
            GroupStart: 1,
            noRecords: false,
            BillingTicketType: '',
            OneCallTicketType: '',
            CreatedBy: '',
            ticketSourceId: 0,
            EditData: '',
            showTickectSource: false,
            ShowTicketMapping: false,
            showBillingTicketList: false,
            showExpanded: '',
            StatusMappingList: [],
            index: 0,
            MappedTicketId: '',
            StatusId: '',
            manageTicketSourceId: 0,
            IsTicketTypeView: true,
            ShowTicketType: false,
            listheaderbuttons: Schema.viewHistoryButtons,
            leftSearchFields: Schema.leftHistorySearchSchema,
            Headers: [{
                "COLUMN_NAME": "ticket_number",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "ticket_subject",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "UserAssignedTo",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "User Assigned To",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "LastActivityDoneBy",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Last Activity Done By",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "type",
                "DATA_TYPE": "",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Accepted/Rejected",
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date - Time",
                "DISPLAY_ORDER": 5,
            },
            ],
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        await this.FetchRecords();  
        //  setTimeout(async function () {
        //     await Schema.bindleftCommonSearchdropdown(vm, "Ticketing", "Priority", vm.TabList, vm.AllCount);
        // }, 2000);     
        await Schema.bindViewHistoryleftCommonSearchdropdown(vm, "Ticketing", "FSM_USERS");
    },
     FilterData: function (pageNum, pageSize, sortBy, sortExp, searchcondition, filterItemData) {
            this.pageNum = pageNum;
            this.pageSize = pageSize;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.searchCondition = searchcondition;
            if (typeof filterItemData != null)
                this.appliedFilter.push(filterItemData);
            this.FetchRecords();
        },
    methods: {
        openAcceptancePending: function () {
            debugger;
            var vm = this;
            vm.$router.push({ name: 'PendingAcceptanceList' });
        },
        sortdata : function (s) {
            var SortDir = "sort tb_headerSortDown";
            if(s == 'UserAssignedTo')
            {
                s = 'user_assigned_to'
            }
            else if(s == 'LastActivityDoneBy')
            {
                s = 'last_activity_done_by'
            }
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchRecords();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        commonHeaderButtonClick: function (event)  {
            switch (event.callbackfunction) {                
                case 'openAcceptancePending':
                    this.openAcceptancePending();
                    break;              
            }
        },
           leftCommonSearch: function (value) {
            debugger;
            this.searchCondition = value.searchCondition;
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            // if (params.hasOwnProperty('Date')) {
            //     this.Date = params['Date'];
            //     this.searchCondition = ''
            // }
            // else {
            //     this.Date = ''
            // }
            // if (params.hasOwnProperty('ticketNumber')) {
            //     this.ticketNumber = params['ticketNumber'];
            //     this.searchCondition = ''
            // }
            // else {
            //     this.ticketNumber = ''
            // }
            // if (params.hasOwnProperty('assigned_to')) {
            //     this.searchCondition  = params['assigned_to'];                
            // }
            // else {
            //     this.searchCondition  = ''
            // }
            // if (params.hasOwnProperty('Accept')) {
            //     this.Accept = params['Accept'];
            //     this.searchCondition = ''
            // }
            // else {
            //     this.Accept = ''
            // }   
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            this.FetchRecords();
        },
        pagerMethod: function (value) {

            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        FetchRecords: function () {
            debugger;
            var vueObj = this;          
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }

            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pagesize=${vueObj.PageSize}&pagenumber=${vueObj.PageNumber}`;
            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + encodeURIComponent(this.searchCondition);
            }

            if (srchCondition != "") {
                url = url + "&search=" + srchCondition;
            }

            DataServices.GetViewHistoryPendingAcceptanceListing(url).then(function (response) {
                debugger;
                vueObj.ViewHistoryList = [];
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                vueObj.TotalRecords = 0;
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.ViewHistoryList = response.data.result.data;

                        if (vueObj.ViewHistoryList.length > 0) {
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.NoRecord = false;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                        }
                        else {
                            vueObj.NoRecord = true;
                        }
                    }
                    else {
                        vueObj.NoRecord = true;
                    }
                    // setTimeout(function () { vueObj.CheckBoxBootstrap() }, 100);
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                }

            });
        },
    }

}
</script>
